body{
    background-color: aliceblue;
}

h1{
    color: white;
    margin: 10px;
    display: block;
    text-align: center;
    font-family: Arial, sans-serif;
    font-size: 45px;
}

.game{
    display: flex;
    align-items: center;
    height: 800px;
    width: 600px;
    border: 1px solid black;
    border-radius: 5%;
    background-color: black;
}

.home{
    margin-top: 30px;
    padding: 40px;
    width: 700px;
    height: 600px;
    border: 2px solid black;
    border-radius: 5%;
    background-color: black;
}

.container{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.left{
    display: flex;
    margin: 20px;
    height: 400px;
}

.right{
    display: flex;
    margin-right: 50px;
    height: 400px;
    width: 250px;
}

canvas{
    display: inline-block;
    margin-left: 0 auto;
    margin-right:0 auto;
    color: white;
}
.logo{
    display: block;
    margin: 0 auto;
    margin-top: 90px;
    width: 400px;
    height: 200px;
}
button{
    font-size: 18px;
    display:block;
    width: 120px;
    height: 50px;
    background-color: #6b64e5;
    border: 2px solid white;
    border-radius: 5%;
    margin: 10px auto;
    box-shadow: -1.5px 1.5px red, -1px 1px orange, -1px 1px orange;
}

.gameBackButton{
    position: relative;
    bottom: 270px;
    right: 600px;
    font-size: 15px;
    font-weight: bold;
    display:block;
    width: 70px;
    height: 30px;
    background-color: #6b64e5;
    border: .5px solid white;
    border-radius: 10%;
    margin: 10px auto;
}

button:hover{
    cursor: pointer;
    background-color: white;
}

li{
    display: block;
    padding: 5px;
    margin-top: 20px;
    margin-left: 200px;
    margin-right: 200px;
    font-size: 20px;
    border: 1px solid black;
    border-radius: 5%;
    color: white;
}

li:hover{
    background-color: #6b64e5;
    cursor: pointer;
}

.game-logo{
    position: relative;
    height: 70px;
    bottom: 350px;
    right: 660px;
    margin-left: 225px;
}

.game-logo:hover{
    cursor: pointer;
}

.hidden{
    display: none;
}

.all-stats{
    margin-top: 40px;
    margin-left: 150px;
    margin-right: 150px;
    border: 2px solid #879cf2;
    border-radius: 5%;

}

h2{
    text-align: center;
    color: #879cf2;
    font-size: 30px;
}

.instructions{
    display: block;
    margin: 0 auto;
    width: 500px;
}

.end-game{
    display: block;
    margin: 0 auto;
    width: 400px;
    height: 200px;
}

.links-songs-screen{
    position: relative;
    top: 321px;
}

.links-home{
    top: 120px;
    position: relative;
}

.link{
    width: 50px;
    margin: 3px;
    border: 1px solid black;
    border-radius: 10%;
}

.link:hover{
    cursor: pointer;
    background-color: white;
}

.start-button{
    height: 250px;
    width: 500px;
    position: relative;
    right: 870px;
}

.start-button:hover{
    cursor: pointer;
}